import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { InputGroupInline, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../models/dtos/ger/printerDataBasic';
import { DATES } from '../../../../../models/enumerated/ger/printerConfigTypeEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportFormView from '../../../report/ReportFormView';
import { CLASS_KEY, RegistroEntradaSaidaRV } from './registroEntradaSaidaRV';

export const REPORT_EVENT: string = 'printResumoDiarioSaida';
export const REPORT_TITLE = 'Resumo Diário de Saídas';

const BASIC_DATA: PrinterDataBasic = {
  ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY, DATES.value),
  dtFinal: '31/12/2017',
  dtInicial: '01/12/2017',
};

export default function ResumoDiarioSaidaReportView(props: WindowComponentProps) {
  const history = useHistory();

  const formProps = useForm<RegistroEntradaSaidaRV>({ mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa !== null) {
        const id = globalReducer.globalParameter.empresa.id;
        setTimeout(() => {
          formProps.setValue('idGerEmpresa', id);
          formProps.setValue('origemLancamentoInt', null);
          formProps.setValue('orderByNumero', false);
          formProps.setValue('armazenarPaginaInicial', false);
          formProps.setValue('resumo', true);
          formProps.setValue('totalizarDia', false);
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      {...props}
      title={REPORT_TITLE}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
      wikiId={6713}
    >
      <ReportFormView formProps={formProps} printerData={BASIC_DATA} history={history}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />

        <InputGroupInline formProps={formProps} id="origemLancamentoInt" hidden={true} />

        <InputGroupInline formProps={formProps} id="orderByNumero" hidden={true} />

        <InputGroupInline formProps={formProps} id="armazenarPaginaInicial" hidden={true} />

        <InputGroupInline formProps={formProps} id="resumo" hidden={true} />

        <InputGroupInline formProps={formProps} id="totalizarDia" hidden={true} />

        <Row>
          <Col>
            <CustomInput
              type="switch"
              id="exibirValoresZerados"
              name="exibirValoresZerados"
              label="Desconsiderar notas canceladas"
              innerRef={formProps.register}
            />
          </Col>
        </Row>
      </ReportFormView>
    </WindowComponent>
  );
}
